@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
body {
  margin: 0;
  font-family: "Open Sans", "Arial", sans-serif;
}
img {
  max-width: 100%;
  height: auto;
}
a {
  text-decoration: none;
}
.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.header {
  position: fixed;
  right: 0;
  z-index: 2;
  width: 100%;
  background: linear-gradient(to right, #60f0dd, #00B0C7);
  border-bottom: 3px solid #FFFFFF;
}
.header__wrap {
  padding: 20px;
}
.header__top-wrap {
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
}
.header__toggle {
  font-size: 0;
  line-height: 0;
  color: transparent;
  width: 30px;
  height: 30px;
  background-image: url("../img/menu-open.svg");
  background-repeat: no-repeat;
  background-size: 30px;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.header__toggle--show {
  background-image: url("../img/menu-close.svg");
}
.header__toggle:hover,
.header__toggle:focus {
  opacity: 0.6;
  outline: none;
}
.header__toggle:active {
  opacity: 0.3;
  outline: none;
}
@media (min-width: 620px) {
  .header__wrap {
    padding: 20px 30px;
  }
}
@media (min-width: 1200px) {
  .header__wrap {
    display: flex;
    justify-content: space-between;
    width: 1200px;
    margin: 0 auto;
    padding: 25px 40px 20px 40px;
  }
  .header__toggle {
    display: none;
  }
}
.languages-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  justify-content: space-between;
}
.languages-list__item:not(:last-child) {
  margin-right: 15px;
}
.languages-list__button {
  font-size: 0;
  line-height: 0;
  color: transparent;
  display: block;
  width: 30px;
  height: 30px;
  background-repeat: no-repeat;
  border-radius: 50%;
}
.languages-list__button--russian {
  background-image: url("../img/russia.svg");
}
.languages-list__button--english {
  background-image: url("../img/united-kingdom.svg");
}
.languages-list__button--japanese {
  background-image: url("../img/japan.svg");
}
.languages-list__button:hover,
.languages-list__button:focus {
  box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.4);
  outline: none;
}
.languages-list__button:active {
  box-shadow: 0 2px 10px 0 rgba(0, 176, 199, 0.8);
  outline: none;
}
.nav-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.nav-list_item {
  padding-top: 15px;
}
.nav-list_item:first-child {
  padding-top: 30px;
}
.nav-list_item:last-child {
  padding-bottom: 15px;
}
.nav-list_link {
  display: inline-block;
  vertical-align: middle;
  font-weight: 700;
  color: #FFFFFF;
  text-transform: uppercase;
}
.nav-list_link:hover,
.nav-list_link:focus {
  color: #60f0dd;
  outline: none;
}
.nav-list_link:active {
  opacity: 0.3;
  outline: none;
}
@media (min-width: 620px) {
  .nav-list_link {
    font-size: 18px;
  }
}
@media (min-width: 1200px) {
  .nav-list {
    flex-direction: row;
    align-items: flex-start;
  }
  .nav-list_item {
    padding-top: 0;
    padding-right: 30px;
  }
  .nav-list_item:first-child {
    padding-top: 0;
  }
  .nav-list_item:last-child {
    padding-bottom: 0;
    padding-right: 0;
  }
}
.main-nav {
  display: block;
}
.main-nav--hidden {
  display: none;
}
@media (min-width: 1200px) {
  .main-nav {
    display: flex;
  }
  .main-nav--hidden {
    display: flex;
  }
}
.about-me {
  padding: 95px 20px 20px 20px;
}
.about-me__wrap {
  display: flex;
  align-items: center;
}
.about-me__image {
  max-width: 100px;
}
.about-me__info-wrap {
  margin-left: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.about-me__info-wrap p {
  margin: 0;
  font-size: 14px;
}
.about-me__links-wrap {
  margin-left: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.about-me__links-wrap a {
  font-size: 0;
  line-height: 0;
  color: transparent;
  border-radius: 50%;
}
.about-me__links-wrap a.about-me__link--stack-overflow,
.about-me__links-wrap a.about-me__link--kwork {
  border-radius: 0;
}
.about-me__links-wrap a:not(:last-child) {
  margin-bottom: 20px;
}
.about-me__links-wrap a:hover,
.about-me__links-wrap a:focus {
  box-shadow: 0 2px 10px 0 rgba(0, 176, 199, 0.8);
  outline: none;
}
.about-me__links-wrap a:active {
  box-shadow: 0 2px 10px 0 #00b0c7;
  outline: none;
}
@media (min-width: 620px) {
  .about-me {
    padding: 105px 30px 30px 30px;
  }
  .about-me__wrap {
    align-items: flex-end;
  }
  .about-me__info-wrap {
    margin-left: 30px;
  }
  .about-me__info-wrap p {
    font-size: 18px;
  }
  .about-me__links-wrap {
    flex-direction: row;
    align-items: flex-end;
  }
  .about-me__links-wrap a:not(:last-child) {
    margin-bottom: 0;
    margin-right: 30px;
  }
}
@media (min-width: 1200px) {
  .about-me {
    margin: 0 auto;
    padding: 115px 40px 40px 40px;
    width: 1200px;
  }
  .about-me__wrap {
    align-items: flex-start;
  }
  .about-me__name {
    font-size: 28px;
  }
  .about-me__image {
    max-width: 250px;
    width: 250px;
  }
  .about-me__info-wrap {
    margin-left: 40px;
  }
  .about-me__info-wrap p {
    font-size: 26px;
  }
}
.tech-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
}
.tech-list__item {
  position: relative;
  padding-left: 25px;
}
.tech-list__item:not(:last-child) {
  margin-bottom: 15px;
}
.tech-list__item::before {
  content: "";
  position: absolute;
  left: 0;
  top: 2px;
  width: 16px;
  height: 16px;
  background-image: url("../img/tick.svg");
  background-size: 16px;
  background-repeat: no-repeat;
}
@media (min-width: 620px) {
  .tech-list__item {
    padding-left: 30px;
  }
  .tech-list__item:not(:last-child) {
    margin-bottom: 20px;
  }
}
.markups-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@supports (display: grid) {
  .markups-list {
    display: grid;
    grid-template-columns: repeat(2, 49%);
    grid-auto-rows: auto;
    justify-items: center;
    grid-gap: 2%;
    margin-bottom: 20px;
  }
}
.markups-list__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 20px;
  max-width: 270px;
}
@supports (display: grid) {
  .markups-list__item {
    grid-column: span 1;
    margin-bottom: 0;
  }
}
.markups-list__item .markups-list__link:last-child {
  border-left: 1px solid #FFFFFF;
}
.markups-list__links-wrap {
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.markups-list__link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50%;
  padding: 10px 15px;
  font-size: 14px;
  color: #FFFFFF;
  text-align: center;
  text-transform: uppercase;
  background-color: #00B0C7;
}
.markups-list__link:hover,
.markups-list__link:focus {
  opacity: 0.6;
  outline: none;
}
.markups-list__link:active {
  opacity: 0.3;
  outline: none;
}
.markups-list__image {
  display: block;
}
.markups-list__description-wrap {
  display: flex;
  flex-direction: column;
  width: 100%;
}
.markups-list__description-button {
  display: block;
  width: 100%;
  padding: 10px 15px;
  font-size: 14px;
  color: #FFFFFF;
  text-align: center;
  text-transform: uppercase;
  background-color: #00B0C7;
  border: none;
  border-top: 1px solid #FFFFFF;
  cursor: pointer;
}
.markups-list__description-button:hover,
.markups-list__description-button:focus {
  opacity: 0.6;
  outline: none;
}
.markups-list__description-button:active {
  opacity: 0.3;
  outline: none;
}
.markups-list__description {
  display: block;
}
.markups-list__description--hidden {
  display: none;
}
@media (min-width: 620px) {
  .markups-list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: flex-start;
    justify-content: center;
  }
  @supports (display: grid) {
    .markups-list {
      display: grid;
      grid-template-columns: repeat(4, 23%);
      grid-auto-rows: auto;
      grid-gap: 2%;
      margin-bottom: 0;
    }
  }
  .markups-list__item {
    margin-bottom: 30px;
    margin-right: 30px;
    max-width: 250px;
  }
  @supports (display: grid) {
    .markups-list__item {
      grid-column: span 1;
      max-width: 100%;
      margin-bottom: 0;
      margin-right: 0;
    }
  }
}
@media (min-width: 1200px) {
  .markups-list {
    display: flex;
    justify-content: flex-start;
  }
  @supports (display: grid) {
    .markups-list {
      display: grid;
      grid-template-columns: repeat(4, 23%);
      grid-auto-rows: auto;
      grid-gap: 2%;
      margin-bottom: 0;
    }
  }
  .markups-list__item {
    margin-right: 0;
    max-width: 260px;
  }
  .markups-list__item:not(:nth-child(4n)) {
    margin-right: 10px;
  }
  @supports (display: grid) {
    .markups-list__item {
      grid-column: span 1;
      max-width: 100%;
      margin-bottom: 0;
      margin-right: 0;
    }
    .markups-list__item:not(:nth-child(4n)) {
      margin-right: 0;
    }
  }
}
.graphic-editors-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.graphic-editors-list__item {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 140px;
  margin-bottom: 20px;
}
.graphic-editors-list__item:hover .graphic-editors-list__title {
  display: block;
}
.graphic-editors-list__title {
  position: absolute;
  top: 68px;
  left: 50%;
  z-index: 1;
  display: none;
  padding: 10px 15px;
  margin-left: -40px;
  text-align: center;
  color: #00B0C7;
  background-color: #FFFFFF;
  border-radius: 4px;
}
.graphic-editors-list__title::after {
  position: absolute;
  content: "";
  z-index: 1;
  top: -8px;
  left: 50%;
  margin-left: -8px;
  width: 0;
  height: 0;
  border-left: 8px solid transparent;
  border-right: 8px solid transparent;
  border-bottom: 8px solid #FFFFFF;
}
@media (min-width: 620px) {
  .graphic-editors-list {
    flex-wrap: wrap;
    justify-content: center;
  }
  .graphic-editors-list__item {
    width: auto;
    margin-bottom: 30px;
  }
  .graphic-editors-list__item:not(:last-child) {
    margin-right: 40px;
  }
}
.certificates-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
}
.certificates-list__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 10px;
}
.certificates-list__link {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.certificates-list__title {
  text-align: center;
  color: #00B0C7;
}
@media (min-width: 620px) {
  .certificates-list {
    flex-direction: row;
    align-items: flex-start;
    justify-content: center;
    flex-wrap: wrap;
    margin-top: 30px;
  }
  .certificates-list__item {
    max-width: 230px;
    margin-right: 30px;
  }
}
@media (min-width: 1200px) {
  .certificates-list {
    justify-content: flex-start;
  }
  .certificates-list__item {
    margin-right: 0;
    max-width: 260px;
  }
  .certificates-list__item:not(:nth-child(4n)) {
    margin-right: 25px;
  }
}
.contacts {
  width: 100%;
  background-color: #2C2C2C;
}
.contacts__wrap {
  display: flex;
  flex-direction: column;
  padding: 40px 20px 20px 20px;
}
.contacts__links-wrap {
  display: flex;
  justify-content: center;
  margin: 20px 0;
}
.contacts__link {
  font-size: 0;
  line-height: 0;
  color: transparent;
  border-radius: 50%;
}
.contacts__link--stack-overflow,
.contacts__link--kwork {
  border-radius: 0;
}
.contacts__link:hover,
.contacts__link:focus {
  box-shadow: 0 2px 10px 0 rgba(0, 176, 199, 0.8);
  outline: none;
}
.contacts__link:active {
  box-shadow: 0 2px 10px 0 #00b0c7;
  outline: none;
}
.contacts__link--email {
  align-self: center;
  margin-bottom: 20px;
  font-size: 16px;
  color: #00B0C7;
  line-height: 16px;
}
.contacts__link--email:hover,
.contacts__link--email:focus {
  box-shadow: none;
  opacity: 0.6;
}
.contacts__link--email:active {
  box-shadow: none;
  opacity: 0.3;
}
@media (min-width: 620px) {
  .contacts__wrap {
    padding: 50px 30px 20px 30px;
  }
  .contacts__links-wrap {
    justify-content: center;
  }
  .contacts__link:not(:last-child) {
    margin-right: 30px;
  }
}
@media (min-width: 1200px) {
  .contacts__wrap {
    padding: 50px 40px 20px 40px;
    margin: 0 auto;
    width: 1200px;
  }
}
